
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@font-face {
  font-family: Manga-Titles;
  src: url(/src/fonts/1083-font.ttf);
}
@keyframes zoomAppearing {
  0% {
      transform: scale(0.1);
  }
  100% {
      transform: scale(1.0);
  }
}
@keyframes opacityAppearing {
  0% {
    opacity: 0;
  }
95%{
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes textAppearing {
  0% {
      color: transparent;
  }
  95%{
    color: transparent;
  }
  100% {
      color: var(--very-dark-blue);
  }
}
@keyframes tableLoad {
  0% {
    transform: translateY(100%);
  }
  100%{
    transform: translateY(0);
  }
}
@keyframes loadElementFromLeft {
  0% {
    transform: translateX(-160%);
  }
  100%{
    transform: translateX(0);
  }
}
@keyframes loadGalleryPresentation {
  0% {
    transform: translateX(-100%);
  }
  100%{
    transform: translateX(0%);
  }
}
@keyframes loadAboutPresentation {
  0% {
    transform: translateX(100%);
  }
  100%{
    transform: translateX(0%);
  }
}
@keyframes rotateAngle {
	0% {
		transform: rotateY(0deg) rotateX(10deg);
		animation-timing-function: cubic-bezier(0.61, 1, 0.88, 1);
	}
	25% {
		transform: rotateY(20deg) rotateX(10deg);
	}
	50% {
		transform: rotateY(0deg) rotateX(10deg);
		animation-timing-function: cubic-bezier(0.61, 1, 0.88, 1);
	}
	75% {
		transform: rotateY(-20deg) rotateX(10deg);
	}
	100% {
		transform: rotateY(0deg) rotateX(10deg);
	}
}

@keyframes translateWobble {
  0% {
		opacity: 0;
		transform: translate3d(
			calc(var(--z) * 0px), 
			calc(var(--z) * 0px), 
			calc(var(--z) * 0px)
		);
  }
  16% {
		transform: translate3d(
			calc(var(--z) * 11vh), 
			calc(var(--z) * 11vh), 
			calc(var(--z) * 11vh)
		);
  }
  28% {
		opacity: 1;
		transform: translate3d(
			calc(var(--z) * 7vh), 
			calc(var(--z) * 7vh), 
			calc(var(--z) * 7vh)
		);
  }
  44% {
		transform: translate3d(
			calc(var(--z) * 13vh), 
			calc(var(--z) * 13vh), 
			calc(var(--z) * 13vh)
		);
  }
  59% {
		transform: translate3d(
			calc(var(--z) * 15vh), 
			calc(var(--z) * 15vh), 
			calc(var(--z) * 15vh)
		);
  }
  73% {
		transform: translate3d(
			calc(var(--z) * 20vh), 
			calc(var(--z) * 20vh), 
			calc(var(--z) * 20vh)
		);
  }
	88% {
		opacity: 1;
		transform: translate3d(
			calc(var(--z) * 9vh), 
			calc(var(--z) * 9vh), 
			calc(var(--z) * 9vh)
		);
  }
  100% {
		opacity: 1;
		transform: translate3d(
			calc(var(--z) * 100px), 
			calc(var(--z) * 100px), 
			calc(var(--z) * 100px)
		);
  }
}
