#exp-logo{
  z-index: 0;
  display: block;
	position: relative;
  cursor: pointer;
  width: 6vw;
  height: 6vw;
} 
.exp-logo {
	display: block;
	position: relative;
  margin-bottom: 2vh;
  padding: 0.5vh 0.2vw;

	cursor: pointer;

	border: none;
	border-radius: .2vw;


	mix-blend-mode: color-dodge;
	perspective: 25vw;
	transform-style: preserve-3d;

}
.exp-logo::before, .exp-logo::after {
		--z: 1vw;
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		content: '';
		width: 100%;
		height: 100%;
		opacity: 0;
		mix-blend-mode: inherit;
		border-radius: inherit;
		transform-style: preserve-3d;
		transform: translate3d(
			calc(var(--z) * 0px), 
			calc(var(--z) * 0px), 
			calc(var(--z) * 0px)
		);
	}
	.back-button{
    display: block;
    animation: 2s cubic-bezier(0.68, -0.6, 0.32, 1.6) 0s 1 loadElementFromLeft;
    z-index: 10;
    text-decoration: none;
    color: var(--white);
    position: absolute;
    left: 5%;
    font-size: 4vw;
    top: 10%;
    padding: 0%;
    text-align: center;
  }
  .back-button::before{
    content: "";
    left: -100%;
    color: yellowgreen;
  }
  .back-button:hover{
    cursor: pointer;
    color: yellowgreen;
  }
	.exp-logo::after {
		background-color: #5D00FF;
	}
	
	.exp-logo::before {
		background-color: #FF1731;
	}
	
	.exp-logo:hover {
		background-color: #FFF65B;
		transition: background .3s 0.1s;
	}
	
	.exp-logo:hover:before {
		--z: 0.04;
		animation: translateWobble 2.2s ease forwards;
	}
	
	.exp-logo:hover:after {
		--z: -0.06;
		animation: translateWobble 2.2s ease forwards;
	}


